import React from 'react';
import "../css/Projects.css";
import Popup from "reactjs-popup";
import CPF from "../media/CPFHackathon.png";
import Makerthon from "../media/Makerthon.jpg";
import Invest from "../media/Invest.jpg";
import SoftEng from "../media/SoftEng.jpg";
import DB from "../media/DB.png";

const CPF_TEXT = "Worked as a team of 5 to redesign the CPF portal using ReactJS and ExpressJS. Other than a responsive calculator, Azure Personalizer API was also used to design a recommendation bar customized for each user's specific needs.";
const MAKERTHON_TEXT = "Designed a web application with a team of 4 using MeteorJS for the elderly to create events and meet up for common events. Firebase authentication and Firestore database was used for this project.";
const INVEST_TEXT = "Worked with quantitative finance specialists to predict the movement of AAPL stock prices. Pandas dataframes & NLP libraries such as sklearn were used to analyze the stock movements.";
const SE_TEXT = "As a student of Introduction to Software Engineering, I designed two project management software named Duke & +Work using Java.";
const DB_TEXT = "As part of the Database Systems module, I worked with my team on a web application that allows users to find potential caretakers for their pets and caretakers to find potential clients. Built on PERN stack, I worked on the backend REST APIs, processing of data, database schema and complex database queries."

const CPF_LINK = "https://github.com/ArunBeCoding/cpf-Monkeybrain-UI";
const MAKERTHON_LINK = "https://github.com/namiwa/makerthon2020";
const INVEST_LINK = "https://github.com/NUS-invest-QF-NLP/main";
const SE_LINK = "https://github.com/AY1920S1-CS2103T-T12-1/main";
const DB_LINK = "https://github.com/CareYourPets";

const HACKATHON_TEXT = "These hackathons have played a huge role in my learning. Even though I have not won any hackathons YET, I feel grateful to have gone through these experiences. As such, I believe it is important to display these hackathons as lessons learnt from my failures";

class ProjPopup extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
        <Popup trigger={open => (<button className="ProjBtn">Learn about {this.props.name}</button>)} modal closeOnDocumentClick>
                {close => (
                    <div className="Popup">
                        <h2>{this.props.name}</h2> 
                        <div className="content">
                            <p>{ this.props.text }</p>
                        </div>
                        <div className="actions">
                            <div className="SpecButton">
                                <button onClick={() => window.open(this.props.link, "_blank")}>
                                    View on Github! 
                                </button>
                            </div>
                            <div className="SpecButton">
                                <button onClick={() => {close();}}>
                                    Sounds Awesome, Arun!
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </Popup>
        );
    }
}

class Proj extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return(
            <div className="ProjSquare">
                <div className="Container">
                    <img className="ProjPic" src={this.props.pic}></img>
                    <ProjPopup name={this.props.name} text={this.props.text} link={this.props.link}/>
                </div>
            </div>
        );
    }
}

class ProjectsInfo extends React.Component {
    render() {
        return(
            <div className="ProjectsContent">
                <table className="center">
                    <tr>
                        <td><Proj name="CPF Hackathon" pic={ CPF } text={CPF_TEXT} link={CPF_LINK}/></td>
                        {/* <td><Proj name="NUS Makerthon" pic={ Makerthon } text={MAKERTHON_TEXT} link={MAKERTHON_LINK}/></td> */}
                        <td><Proj name="NUS Makerthon" pic={ DB } text={DB_TEXT} link={DB_LINK}/></td>
                    </tr>
                    <tr>
                        <td><Proj name="NUS Invest" pic={ Invest } text={INVEST_TEXT} link={INVEST_LINK}/></td>
                        <td><Proj name="+Work and Duke" pic={ SoftEng } text={SE_TEXT} link={SE_LINK}/></td>
                    </tr>
                </table>
            </div>
        );
    }
}

class HackathonsInfo extends React.Component {
    render() {
        return(
            <div className="HackathonsContent">
                <div className="HackExpln">
                    <p>{ HACKATHON_TEXT }</p>
                </div>
                <div className="HackList">
                    <ul>
                        <li>Shopee Code League 2020</li>
                        <li>DSTA Brainhack CDDC 2020</li>
                        <li>CPF Hackathon</li>
                        <li>NUS Makerthon 2020</li>
                        <li>National Blockchain Challenge 2019</li>
                    </ul>
                </div>
            </div>
        );
    }
}

function SkillsLanguage() {
    return (
        <div className="SkillsCard SkillsLanguage">
            <ul>
                <lh>Languages</lh>
                <li>Java</li>
                <li>Python</li>
                <li>C</li>
                <li>JavaScript</li>
            </ul>
        </div>
    );
}

function SkillsFrameworks() {
    return (
        <div className="SkillsCard SkillsFrameworks">
            <ul>
                <lh>Framework</lh>
                <hr />
                <li className="subHeader">Full Stack</li>
                <li>reactjs, firebase, Flask, Spring Boot</li>
                <hr />

                <li className="subHeader">Data Analytics Libraries</li>
                <li>SKlearn, Numpy, Pandas</li>
                <hr />

                <li className="subHeader">Databases</li>
                <li>MongoDB, PostgreSQL</li>
                <hr />

                <li className="subHeader">DevOps</li>
                <li>GitLab, Docker</li>
            </ul>
        </div>
    );
}

function SkillsCertifications() {
    return(
        <div className="SkillsCard SkillsCertifications">
            <ul>
                <lh>Certifications</lh>
                <li>AZ-900: Microsoft Azure Fundementals</li>
            </ul>
        </div>
    );
}

class SkillsInfo extends React.Component {
    render() {
        return(
            <div className="SkillsContent">
                {SkillsLanguage()}
                {SkillsFrameworks()}
                {SkillsCertifications()}
            </div>
        );
    }
}

class Projects extends React.Component {
    state = {
        isProjects: true,
        isHackathons: false,
        isSkills: false
    }

    ProjectsHandler = () => {
        this.setState({
            isProjects: true,
            isHackathons: false,
            isSkills: false
        })
    }

    HackathonsHandler = () => {
        this.setState({
            isProjects: false,
            isHackathons: true,
            isSkills: false
        })
    }

    SkillsHandler = () => {
        this.setState({
            isProjects: false,
            isHackathons: false,
            isSkills: true
        })
    }

    infoHandler() {
        if(this.state.isProjects) {
            return(
                <ProjectsInfo/>
            );
        } else if (this.state.isHackathons) {
            return (
                <HackathonsInfo/>
            );
        } else if (this.state.isSkills) {
            return (
                <SkillsInfo/>
            );
        }
    }

    render() {
        return(
            <div className="Projects">
                <div className="Card">
                    <div className="Options">
                        <ul>
                            <li><button className={this.state.isProjects? "ActiveButton" : "SilentButton"} onClick={this.ProjectsHandler}>Projects</button></li>
                            <li><button className={this.state.isHackathons? "ActiveButton" : "SilentButton"} onClick={this.HackathonsHandler}>Hackathons</button></li>
                            <li><button className={this.state.isSkills? "ActiveButton" : "SilentButton"} onClick={this.SkillsHandler}>Skills</button></li>
                        </ul>
                    </div>
                    { this.infoHandler() }
                </div>
            </div>
        );
    }
}

export default Projects;