import React from 'react';
import '../css/About.css';
import profile from "../media/Profile_Pic.jpg";
import Popup from "reactjs-popup";

class EducationPopup extends React.Component {
    render() {
        return (
            <div className="Modules">
                <Popup 
                    trigger={open => (
                        <button className="button">Learn more</button>
                    )}
                    modal
                    closeOnDocumentClick
                >
                {close => (
                    <div className="Popup">
                        <div> <h2>Relevant Modules Taken</h2> </div>
                        <div className="content">
                          <h3>Programming and Discrete Structures</h3>
                          <p>Programming Methodology I | Programming Methodology II | Discrete Structures</p>
                          <hr/>
                          <h3>Data Structures</h3>
                          <p>Data Structures and Algorithms | Design and Analysis of Algorithms</p>
                          <hr/>
                          <h3>Software Engineering</h3>
                          <p>Introduction to Software Engineering</p>
                          <hr/>
                          <h3>Operating Systems</h3>
                          <p>Computer Organization | Introduction to Operating Systems</p>
                          <hr/>
                          <h3>Computer Security</h3>
                          <p>Introduction to Information Security | Computer Security | Computer Security Practice</p>
                          <hr/>
                          <h3>Networks</h3>
                          <p>Introduction to Computer Networks</p>
                          <hr/>
                          <h3>Machine Learning</h3>
                          <p>Machine Learning | ColumbiaX - CSM.102x</p>
                          <hr/>
                          <h3>Database</h3>
                          <p>Database Systems</p>
                        </div>
                        <div className="actions">
                            <button className="button" onClick={() => {close();}}>
                                I'm done reading!
                            </button>
                        </div>
                    </div>
                )}
                </Popup>
            </div>
        );
    }
}

class About extends React.Component {
  state = {
    isAbout: true
  }
  AboutHandler = () => {
    this.setState({
        isAbout: true
    })
  }

  EducationHandler = () => {
    this.setState({
        isAbout: false
    })
  }

  aboutHandler() {
    if(this.state.isAbout) {
      return (
        <div>
          <img className="Profile" src={profile} alt="Profile Photo"></img>
          <p className="AboutMeText">Hi, I am Arun Kumarr. I am currently doing my third year in NUS Computer Science. With a strong passion to learn and build solution-oriented products, I have been a part of numerous projects and competitions, in addition to my internships in various roles. My key interests lie in ​information security​, ​software engineering ​and ​cloud computing​.</p>
        </div>
      );
    } else {
      return (
        <div className="Education">
          <h3>National University of Singapore, Singapore</h3>
          <p>Bachelors in Computer Science</p>
          <p>Expected time of graduation: May 2022</p>
          <EducationPopup/>
        </div>
      );
    }
  }
  render() {
    return (
      <div className="Card">
        <div className="Option">
          <ul>
            <li><button className={this.state.isAbout ? "ActiveButton" : "SilentButton"} onClick={this.AboutHandler} autoFocus>About Me</button></li>
            <li><button className={this.state.isAbout ? "SilentButton" : "ActiveButton"} onClick={this.EducationHandler} >Education</button></li>
          </ul>
        </div>
        { this.aboutHandler() }
      </div>
    );
  }
}

export default About;